import { createSlice } from "@reduxjs/toolkit";
// import axios from "axios";
const chatSlice = createSlice({
    name: 'chat',
    initialState: {
        isChatBoxShown: false,
        isUserListShown: false,
        selectedUser: 0,
        contacts: [1],
        messages: [],
        unreadsCount: 0
    },
    reducers: {
        setChatBoxShown: (state, isChatBoxShown) => {
            // if (isChatBoxShown) {
            //     axios.post('/api/chat').then(({data: response}) => {
            //         if (response.status !== 0) throw new Error('error');
            //         state.isChatBoxShown = true;
            //     }).catch(err => {
            //         console.error(err);
            //     });
            // } else {
            //     state.isUserListShown = false;
            //     state.isChatBoxShown = isChatBoxShown;
            // }
        },
        setUserListShown: (state, isUserListShown) => {
            state.isUserListShown = isUserListShown;
            if (isUserListShown) state.isChatBoxShown = true;
        },
        setSelectedUserId: (state, selectedUserId) => {
            state.selectedUserId = selectedUserId;
        }
    }
})

export const { setChatBoxShown, setUserListShown, setSelectedUserId } = chatSlice.actions;

export default chatSlice.reducer;


