import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import axios from 'axios';
import { serverUrl } from '../../config';
import toastr from 'toastr';

const AddNew = (props) => {

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [phone, setPhone] = useState('');
    const [gender, setGender] = useState(1);

    const sendUser = () => {
        const userData = {
            name,
            email,
            password,
            phone,
            gender
        }
        axios.post(serverUrl + '/api/user/register', userData)
            .then(response => {
                const { data } = response;
                if(!data.status) {
                    toastr.success('Successfully added.', 'Success');
                    props.addNew(userData);
                }
                else {
                    toastr.warning(data.message, 'Warning');
                }
            })
            .catch(error => {

            })
    }

    return (

        <div className={classNames('md-modal', 'md-effect-1')} id="modal-1">
            <div className="md-content">
                <div className="card" style={{ paddingBottom: 15 }}>
                    <div className="card-header">
                        <h5>New User</h5>
                        <span>Add class of <code>.form-control</code> with <code>&lt;input&gt;</code> tag</span>
                    </div>
                    <div className="card-block">
                        <form>
                            <div className="row">
                                <label className="form-label col-sm-4 col-lg-2 col-form-label">Name</label>
                                <div className="col-sm-8 col-lg-10">
                                    <div className="input-group">
                                        <span className="input-group-text addon-style">
                                            <label className="">
                                                <i className="icofont icofont-user-alt-3"></i>
                                            </label>
                                        </span>
                                        <input type="text" className="form-control" placeholder="Input name"
                                            value={name}
                                            onChange={e => setName(e.target.value)} />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <label className="form-label col-sm-4 col-lg-2 col-form-label">Email</label>
                                <div className="col-sm-8 col-lg-10">
                                    <div className="input-group">
                                        <span className="input-group-text addon-style">
                                            <label className="">
                                                <i className="icofont icofont-ui-email"></i>
                                            </label>
                                        </span>
                                        <input type="text" className="form-control" placeholder="Input email"
                                            value={email}
                                            onChange={e => setEmail(e.target.value)} />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <label className="form-label col-sm-4 col-lg-2 col-form-label">Password</label>
                                <div className="col-sm-8 col-lg-10">
                                    <div className="input-group">
                                        <span className="input-group-text addon-style">
                                            <label className="">
                                                <i className="fa fa-key"></i>
                                            </label>
                                        </span>
                                        <input type="password" className="form-control" placeholder="Input password"
                                            value={password}
                                            onChange={e => setPassword(e.target.value)} />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <label className="form-label col-sm-4 col-lg-2 col-form-label">Phone</label>
                                <div className="col-sm-8 col-lg-10">
                                    <div className="input-group">
                                        <span className="input-group-text addon-style">
                                            <label className="">
                                                <i className="icofont icofont-ui-email"></i>
                                            </label>
                                        </span>
                                        <input type="text" className="form-control" placeholder="Input phone"
                                            value={phone}
                                            onChange={e => setPhone(e.target.value)} />
                                    </div>
                                </div>
                            </div>
                            <div className="row">
                                <label className="form-label col-sm-4 col-lg-2 col-form-label">Gender</label>
                                <div className="col-sm-8 col-lg-10">
                                    <div className="input-group">
                                        <span className="input-group-text addon-style">
                                            <label className="">
                                                <i className="fa fa-tasks"></i>
                                            </label>
                                        </span>
                                        <select name='select' className='form-select form-control-default form-control-capitalize fill' style={{ border: '1px solid 206, 212, 218', fontSize: 14, color: '#212529' }}
                                            value={gender}
                                            onChange={e => setGender(e.target.value)} >
                                            <option value={1}>male</option>
                                            <option value={0}>female</option>
                                        </select>
                                    </div>
                                </div>
                            </div>
                            <div className='row'>
                                <label className="form-label col-sm-4 col-lg-2 col-form-label"></label>
                                <div className='col-sm-8 col-lg-10'>
                                    <div className="input-group">
                                        <div style={{ margin: '0 0 0 auto' }}>
                                            <button className="btn waves-effect waves-light btn-primary" style={{ marginRight: 10 }} onClick={sendUser}>
                                                <i className="icofont icofont-user-alt-3"></i>
                                                Add
                                            </button>
                                            <button className="btn waves-effect waves-light btn-danger md-close" style={{ margin: 0 }}>
                                                <i className="ti-close"></i>
                                                Close
                                            </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>

    );
}

export default AddNew;


