import React, { Fragment, useState, useMemo } from 'react';
import moment from 'moment';
import HeadBar from '../../component/layout/HeadBar';
import NavBar from '../../component/layout/NavBar';
import UserList from '../../component/chat/UserList';
import Message from '../../component/chat/Message';
import PageHeader from '../../component/common/PageHeader';
import classNames from 'classnames';

const pageSize = 10;

const mockheader = [
    'ID',
    'Name',
    'Email',
    'Created Date',
    'Due Date',
    'Amount',
    'Created By',
    'Status',
    'Action'
]

const mockdata = [
    {
        id: '#100091',
        name: 'John Dae',
        email: 'JohnDae@gmail.com',
        created_date: new Date(2023, 5, 20),
        due_date: new Date(2023, 6, 20),
        amount: 24.5,
        created_by: 'admin',
        status: 0
    },
    {
        id: '#100090',
        name: 'James Cooper',
        email: 'JamesCo@gmail.com',
        created_date: new Date(2023, 4, 18),
        due_date: new Date(2023, 5, 23),
        amount: 150,
        created_by: 'admin',
        status: -1
    },
    {
        id: '#100089',
        name: 'Philippe Mercier',
        email: 'Philippe.M@gmail.com',
        created_date: new Date(2023, 4, 25),
        due_date: new Date(2023, 4, 30),
        amount: 560,
        created_by: 'admin',
        status: 2
    },
    {
        id: '#100088',
        name: 'Louis Dupont',
        email: 'LouisDupont@gmail.com',
        created_date: new Date(2023, 5, 20),
        due_date: new Date(2023, 6, 20),
        amount: 154.5,
        created_by: 'admin',
        status: 1
    },
    {
        id: '#100087',
        name: 'Louis Dupont',
        email: 'LouisDupont@gmail.com',
        created_date: new Date(2023, 4, 20),
        due_date: new Date(2023, 6, 20),
        amount: 1500,
        created_by: 'admin',
        status: 0
    }
]

const Invoice = () => {

    const [currentPage, setCurrentPage] = useState(1);

    const currentTableData = useMemo(() => {
        const firstPageIndex = (currentPage - 1) * pageSize;
        const lastPageIndex = firstPageIndex + pageSize;
        return mockdata.slice(firstPageIndex, lastPageIndex);
    }, [currentPage]);

    const statusFormat = (status) => {
        switch (status) {
            case -1:
                return {
                    color: 'label-danger',
                    text: 'Unpaid'
                }
            case 0:
                return {
                    color: 'label-warning',
                    text: 'Unviewed'
                }
            case 1:
                return {
                    color: 'label-primary',
                    text: 'Viewed'
                }
            case 2:
                return {
                    color: 'label-success',
                    text: 'Paid'
                }
        }
    }

    return (

        <Fragment>
            <div className="loader-bg">
                <div className="loader-bar"></div>
            </div>
            <div id="pcoded" className="pcoded">
                <div className="pcoded-overlay-box"></div>
                <div className="pcoded-container navbar-wrapper">
                    <HeadBar />
                    <UserList />
                    <Message />
                    <div className="pcoded-main-container">
                        <NavBar parent={'Invoice'} />
                        <div className="pcoded-content">
                            <PageHeader title={'Invoice'} description={'view money invoices'} icon={'feather icon-home'} />
                            <div className="pcoded-inner-content">
                                <div className="main-body">
                                    <div className="page-wrapper">
                                        <div className="page-body">
                                            <div className="row">
                                                <div className="col-md-12">
                                                    <div className='card'>
                                                        {/* <div className='card-header'>
                                                            <div className='card-header-left'>
                                                                <h5>Search</h5>
                                                            </div>
                                                        </div> */}
                                                        <div className='card-block d-flex'>
                                                            <div className='search-block' style={{ display: 'flex', flexWrap: 'wrap', alignItems: 'stretch', width: '100%' }}>
                                                                <input
                                                                    type="text"
                                                                    className="form-control"
                                                                    placeholder="Enter Keyword"
                                                                    style={{ width: '200px', border: 'none', backgroundColor: 'transparent', boxShadow: 'none' }}
                                                                />
                                                                <span className="input-group-text search-btn" style={{ backgroundColor: 'transparent', border: 'none' }}>
                                                                    <i
                                                                        className="feather icon-search input-group-text"
                                                                        style={{ backgroundColor: 'transparent', border: 'none', color: 'black', cursor: 'pointer' }}
                                                                    ></i>
                                                                </span>
                                                            </div>
                                                            <div className='ml-auto'>
                                                                <button className="btn waves-effect waves-light btn-primary btn-icon">
                                                                    <i className="ti-plus m-0"></i>
                                                                </button>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="col-md-12">
                                                    <div className="card table-card">
                                                        <div className="card-header d-flex">
                                                            <h5>Invoice List</h5>
                                                        </div>
                                                        <div className="card-block">
                                                            <div className="table-responsive">
                                                                <table className="table table-hover m-b-0">
                                                                    <thead>
                                                                        <tr>
                                                                            {
                                                                                mockheader.map((item, key) => (
                                                                                    <th key={key}>{item}</th>
                                                                                ))
                                                                            }
                                                                        </tr>
                                                                    </thead>
                                                                    <tbody>
                                                                        {
                                                                            currentTableData.map((item, key) => (
                                                                                <tr key={key}>
                                                                                    <td>{item.id}</td>
                                                                                    <td>{item.name}</td>
                                                                                    <td>{item.email}</td>
                                                                                    <td>{moment(item.created_date).format('DD-MM-YYYY')}</td>
                                                                                    <td>{moment(item.due_date).format('DD-MM-YYYY')}</td>
                                                                                    <td>{'$' + item.amount.toFixed(2)}</td>
                                                                                    <td>{item.created_by}</td>
                                                                                    <td>
                                                                                        <label className={classNames('form-label label', statusFormat(item.status).color)}>
                                                                                            {statusFormat(item.status).text}
                                                                                        </label>
                                                                                    </td>
                                                                                    <td>
                                                                                        <a href="#">
                                                                                            <i className="fa fa-eye f-w-600 f-16 m-r-15 text-c-blue"></i>
                                                                                        </a>
                                                                                        <a href="#">
                                                                                            <i className="icon feather icon-edit f-w-600 f-16 m-r-15 text-c-green"></i>
                                                                                        </a>
                                                                                        <a href="#">
                                                                                            <i className="feather icon-trash-2 f-w-600 f-16 text-c-red"></i>
                                                                                        </a>
                                                                                    </td>
                                                                                </tr>
                                                                            ))
                                                                        }
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                {/* <div className="col-md-12">
                                                    <div className='card'>
                                                        <div className='card-header'>
                                                            <div className='card-header-left'>
                                                                <h5>Search</h5>
                                                            </div>
                                                        </div>
                                                        <div className='card-block d-flex'>
                                                        </div>
                                                    </div>
                                                </div> */}
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </Fragment>
    );
}

export default Invoice;


