import React, { Fragment } from 'react';
import { Link } from 'react-router-dom';

const pageList = [
    {
        title: 'Dashboard',
        icon: 'feather icon-home',
        link: '/dashboard'
    },
    {
        title: 'Files',
        icon: 'ti-files',
        link: '/dashboard'
    },
    {
        title: 'Jobs',
        icon: 'feather icon-layers',
        link: '#',
        children: [
            {
                title: 'Add',
                to: '/dashboard'
            },
            {
                title: 'List',
                to: '/dashboard'
            },
            {
                title: 'Manage Result',
                to: '/dashboard'
            }
        ]
    },
    {
        title: 'Portfolio',
        icon: 'feather icon-command',
        link: '/dashboard'
    },
    {
        title: 'Enquiry',
        icon: 'fa fa-question-circle-o',
        link: '/dashboard'
    },
    {
        title: 'Contacts',
        icon: 'fa fa-paper-plane-o',
        link: '/dashboard'
    },
    {
        title: 'Invoice',
        icon: 'fa fa-wpforms',
        link: '/invoice'
    },
    {
        title: 'Quotation',
        icon: 'fa fa-edit',
        link: '/dashboard'
    },
    {
        title: 'Users',
        icon: 'ti-user',
        link: '/users'
    }
]

const NavBar = (props) => {

    return (

        <Fragment>
            <div className="pcoded-wrapper">
                <nav className="pcoded-navbar">
                    <div className="nav-list">
                        <div className="pcoded-inner-navbar main-menu">
                            {/* <div className="pcoded-navigation-label">Dashboard</div> */}
                            <div className='mt-3'></div>
                            <ul className="pcoded-item pcoded-left-item">
                                {
                                    pageList.map((item, listKey) => (
                                        <li className={item.children ? 'pcoded-hasmenu' : '' + props.parent === item.title ? ' active' : ''} key={listKey}>
                                            <a href={item.link} className="waves-effect waves-dark">
                                                <span className="pcoded-micon"><i className={item.icon}></i></span>
                                                <span className="pcoded-mtext">{item.title}</span>
                                            </a>
                                            {
                                                !item.children ? '' : (
                                                    <ul className="pcoded-submenu">
                                                        {
                                                            item.children.map((child, childKey) => (
                                                                <li className={props.child === child.title ? 'active' : ''} key={childKey}>
                                                                    <a href={child.link} className="waves-effect waves-dark">
                                                                        <span className="pcoded-mtext">{child.title}</span>
                                                                    </a>
                                                                </li>
                                                            ))
                                                        }
                                                    </ul>
                                                )
                                            }
                                        </li>
                                    ))
                                }
                                {/* <li className={props.parent === 'Dashboard' ? "active" : ''}>
                                    <a href="/dashboard" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="feather icon-home"></i>
                                        </span>
                                        <span className="pcoded-mtext">Dashboard</span>
                                    </a>
                                </li>
                                <li className="">
                                    <a href="#" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="ti-files"></i>
                                        </span>
                                        <span className="pcoded-mtext">Files</span>
                                    </a>
                                </li>
                                <li className="pcoded-hasmenu">
                                    <Link href="#" className="waves-effect waves-dark">
                                        <span className="pcoded-micon"><i className="feather icon-layers"></i></span>
                                        <span className="pcoded-mtext">Jobs</span>
                                    </Link>
                                    <ul className="pcoded-submenu">
                                        <li className="active">
                                            <Link href="#" className="waves-effect waves-dark">
                                                <span className="pcoded-mtext">Add</span>
                                            </Link>
                                        </li>
                                        <li className="">
                                            <Link href="#" className="waves-effect waves-dark">
                                                <span className="pcoded-mtext">List</span>
                                            </Link>
                                        </li>
                                        <li className="">
                                            <Link href="#" className="waves-effect waves-dark">
                                                <span className="pcoded-mtext">Manage Request</span>
                                            </Link>
                                        </li>
                                    </ul>
                                </li>
                                <li className="">
                                    <a href="/portfolio" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="feather icon-command"></i>
                                        </span>
                                        <span className="pcoded-mtext">Portfolio</span>
                                    </a>
                                </li>
                                <li className="">
                                    <a href="/enquiry" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="fa fa-question-circle-o"></i>
                                        </span>
                                        <span className="pcoded-mtext">Enquiry</span>
                                    </a>
                                </li>
                                <li className="">
                                    <a href="/contacts" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="fa fa-paper-plane-o"></i>
                                        </span>
                                        <span className="pcoded-mtext">Contacts</span>
                                    </a>
                                </li>
                                <li className="">
                                    <a href="/invoice" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="fa fa-wpforms"></i>
                                        </span>
                                        <span className="pcoded-mtext">Invoice</span>
                                    </a>
                                </li>
                                <li className="">
                                    <a href="/quotation" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="fa fa-edit"></i>
                                        </span>
                                        <span className="pcoded-mtext">Quotation</span>
                                    </a>
                                </li>
                                <li className={props.parent === 'Users' ? "active" : ''}>
                                    <a href="/users" className="waves-effect waves-dark">
                                        <span className="pcoded-micon">
                                            <i className="ti-user"></i>
                                        </span>
                                        <span className="pcoded-mtext">Users</span>
                                    </a>
                                </li> */}
                            </ul>
                        </div>
                    </div>
                </nav>
            </div>
        </Fragment>

    );

}

export default NavBar;


