import axios from 'axios';
import React, { Fragment, useState } from 'react';
import toastr from 'toastr';
import 'toastr/build/toastr.min.css';
import { Link } from 'react-router-dom';
import { serverUrl } from '../../config';

const SignIn = () => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    const loginClick = (e) => {
        e.preventDefault();
        const user = { email, password };
        axios.post(serverUrl + '/api/user/login', user)
            .then(response => {
                const { data } = response;
                console.log(data);
                if(!data.status) {
                    // toastr.success(`Welcome ${data.user.name}!`, 'Congratulations');
                    localStorage.setItem('token', data.token);
                    console.log(data.token);
                    axios.defaults.headers.common['Authorization'] = data.token;
                    window.location.href = '/dashboard';
                }
                else {
                    toastr.warning(data.message, 'Wrong');
                }
            })
            .catch((error) => {
                console.log(error);
                toastr.warning('Please try again later.', 'No answer from server');
            })
    }

    return (
        <Fragment>
            <div className="theme-loader">
                <div className="loader-track">
                    <div className="preloader-wrapper">
                        <div className="spinner-layer spinner-blue">
                            <div className="circle-clipper left">
                                <div className="circle"></div>
                            </div>
                            <div className="gap-patch">
                                <div className="circle"></div>
                            </div>
                            <div className="circle-clipper right">
                                <div className="circle"></div>
                            </div>
                        </div>
                        <div className="spinner-layer spinner-red">
                            <div className="circle-clipper left">
                                <div className="circle"></div>
                            </div>
                            <div className="gap-patch">
                                <div className="circle"></div>
                            </div>
                            <div className="circle-clipper right">
                                <div className="circle"></div>
                            </div>
                        </div>

                        <div className="spinner-layer spinner-yellow">
                            <div className="circle-clipper left">
                                <div className="circle"></div>
                            </div>
                            <div className="gap-patch">
                                <div className="circle"></div>
                            </div>
                            <div className="circle-clipper right">
                                <div className="circle"></div>
                            </div>
                        </div>

                        <div className="spinner-layer spinner-green">
                            <div className="circle-clipper left">
                                <div className="circle"></div>
                            </div>
                            <div className="gap-patch">
                                <div className="circle"></div>
                            </div>
                            <div className="circle-clipper right">
                                <div className="circle"></div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <section className="login-block">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-sm-12">

                            <form className="md-float-material form-material" onSubmit={loginClick}>
                                <div className="text-center">
                                    <img src={process.env.PUBLIC_URL + '/files/assets/images/logo.png'} alt="logo.png" />
                                </div>
                                <div className="auth-box card">
                                    <div className="card-block">
                                        <div className="row m-b-20">
                                            <div className="col-md-12">
                                                <h3 className="text-center txt-primary">Sign In</h3>
                                            </div>
                                        </div>
                                        <div className="row m-b-20">
                                            <div className="col-md-6">
                                                <div className="d-grid">
                                                    <button className="btn btn-facebook m-b-20">
                                                        <i className="icofont icofont-social-facebook"></i>facebook
                                                    </button>
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="d-grid">
                                                    <button className="btn btn-twitter m-b-20">
                                                        <i className="icofont icofont-social-twitter"></i>twitter
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                        <p className="text-muted text-center p-b-5">Sign in with your regular account</p>
                                        <div className="form-group form-primary">
                                            <input
                                                type="email"
                                                name="user-name"
                                                className="form-control"
                                                required=""
                                                autoComplete='off'
                                                placeholder='Email'
                                                style={{ padding: 10 }}
                                                value={email}
                                                onChange={e => setEmail(e.target.value)}
                                            />
                                            <span className="form-bar"></span>
                                            {/* <label className="form-label float-label">Username</label> */}
                                        </div>
                                        <div className="form-group form-primary">
                                            <input
                                                type="password"
                                                name="user-password"
                                                className="form-control"
                                                required=""
                                                autoComplete='off'
                                                placeholder='Password'
                                                style={{ padding: 10 }}
                                                value={password}
                                                onChange={e => setPassword(e.target.value)}
                                            />
                                            <span className="form-bar"></span>
                                            {/* <label className="form-label float-label">Password</label> */}
                                        </div>
                                        <div className="row m-t-25 text-start">
                                            <div className="col-12">
                                                <div className="checkbox-fade fade-in-primary">
                                                    <label className="form-label">
                                                        <input type="checkbox" value="" />
                                                        <span className="cr"><i
                                                            className="cr-icon icofont icofont-ui-check txt-primary"></i></span>
                                                        <span className="text-inverse">Remember me</span>
                                                    </label>
                                                </div>
                                                <div className="forgot-phone text-end float-end">
                                                    <Link to='/' className="text-end f-w-600">Forgot Password?</Link>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row m-t-30">
                                            <div className="col-md-12">
                                                <div className="d-grid">
                                                    <button
                                                        type="button"
                                                        className="btn btn-primary btn-md waves-effect text-center m-b-20"
                                                    >
                                                        SIGNIN
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </Fragment>
    );
};

export default SignIn;


