import { createSlice } from "@reduxjs/toolkit";
const gloablSlice = createSlice({
    name: 'global',
    initialState: {
        isChatBoxShown: false
    },
    reducers: {
        setChatBoxShown: (state, isChatBoxShown) => {
            state.isChatBoxShown = isChatBoxShown;
            console.log(123);
        }
    }
})

export const { setChatBoxShown } = gloablSlice.actions;

export default gloablSlice.reducer;


