import React from 'react';

import { BrowserRouter, Route, Routes, Navigate } from 'react-router-dom';
import SignIn from './container/auth/SignIn';
import PrivatePage from './container/auth/PrivatePage';
import Dashbaord from './container/page/Dashboard';
import Users from './container/page/Users';
import Invoice from './container/page/Invoice';
import store from './store';
import { useEffect } from 'react';
import ErrorPage from './container/auth/ErrorPage';
import { Provider } from 'react-redux';

function App() {

  useEffect(() => {
    if (window.location.pathname === '/signin' && localStorage.getItem('token')) {
      window.location.href = '/dashboard';
    }
  }, [])

  return (
    <BrowserRouter>
      <Provider store={store}>
        <Routes>
          <Route path="/signin" exact element={<SignIn />} />
          <Route path="/" exact element={<Navigate to='/signin' replace />} />
          <Route path='/dashboard' exact element={<PrivatePage component={<Dashbaord />} />} />
          <Route path='/invoice' exact element={<PrivatePage component={<Invoice />} />} />
          <Route path='/users' exact element={<PrivatePage component={<Users />} />} />
          <Route path='*' element={<ErrorPage />} />
        </Routes>
      </Provider>
    </BrowserRouter>
  );
}

export default App;



